export default {
	data() {
		return {
			oldPassword: '',
			newPassword: '',
			renewPassword: '',
			disable: true,
			loading: false
		}
	},
	watch: {
		oldPassword (val) {
			this.change();
		},
		newPassword (val) {
			this.change();
		},
		renewPassword (val) {
			this.change();
		}
	},
	methods: {
		// 监听输入框
		change () {
			if (this.oldPassword && this.newPassword && this.renewPassword) {
				this.disable = false;
				return;
			}
			this.disable = true;
		},
		// 验证
		check () {
			if (this.oldPassword && this.newPassword && this.renewPassword && this.oldPassword != "" && this.newPassword != "" && this.renewPassword != "") {	
				if (this.newPassword !== this.renewPassword) {
					uni.showToast({
						title: '密码和新密码输不一致',
						icon: 'none'
					})
					return false;
				}
				return true;
			}
			
			uni.showToast({
				title: '请确认数据填写完整',
				icon: "none"
			})
			
			return false;
			
		},
		// 提交
		submit () {
			
			this.loading = true;
			this.disable = true;
			
			if (!this.check()) {
				this.loading = false;
				this.disable = false;
				return;
			}
			uni.showToast({
				title: '验证通过',
				mask: false,
				duration: 1500
			});
			this.loading = false;
			this.disable = false;
		}
	}
}
